import { Fragment } from "react";
import { connect } from "react-redux";

import TeamMembersContainer from "../../../Container/BusinessSettings/TeamMembers";
import TeamMembersInviteForm from "../../../Container/BusinessSettings/TeamMembers/InviteForm";
import { Hidden } from "@mui/material";

import * as actions from "../../../Store/actions/index";

import TeamMemberContext, {
  useTeamMembers,
} from "../../../contexts/TeamMember";

import Layout from "../Layout";

const Index = ({ sendUserInvite }) => {
  const { showInviteForm } = useTeamMembers();

  return (
    <Fragment>
      <Hidden smUp implementation="js">
        <TeamMembersContainer />
        {showInviteForm && <TeamMembersInviteForm />}
      </Hidden>
      <Hidden xsDown implementation="js" only="xs">
        <Layout useFluid>
          <TeamMembersContainer />
          {showInviteForm && <TeamMembersInviteForm />}
        </Layout>
      </Hidden>
    </Fragment>
  );
};

//export default TeamMember;

const mapStateToProps = (state) => {
  return {
    invitedUsers: state.invite.invitedUsers,
    selectedBusiness: state.business.selectedBusiness,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    sendUserInvite: (data, businessId, toastMessage) =>
      dispatch(actions.sendInvite(data, businessId, toastMessage)),
    getAllUserInvites: (toastMessage, businessId) =>
      dispatch(actions.getAllUserInvites(toastMessage, businessId)),
  };
};

const TeamMember = (props: any) => (
  <TeamMemberContext>
    <Index {...props} />
  </TeamMemberContext>
);

export default connect(mapStateToProps, mapDispatchToProps)(TeamMember);
