import axios from "axios";
import { AuthenticatedHeaders } from "utils/ApiHeaders";
import { API, API_V2 } from "../../Backend";
import decryptResponse from "../../Shared/decryptResponse";
import encryptResponse from "../../Shared/encryptResponse";

let cancelToken;

export const getTeamMembersByName = async (data) => {
  if (typeof cancelToken != typeof undefined) {
    cancelToken.cancel("Operation canceled due to new request.");
  }

  try {
    let source = axios.CancelToken.source();

    cancelToken = source;

    const result = await axios({
      url: `${API}/team-member/get`,
      method: "POST",
      headers: {
        Accept: "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
        "X-BusinessId": localStorage.getItem("businessId"),
      },
      cancelToken: source.token,
      data: encryptResponse(data),
    });

    if (result.data) {
      return decryptResponse({ data: result.data });
    }
  } catch (err) {
    return { error: err.message };
  }
};

let cancelToken2;
export const getTeamMembersByNameAdvanced = async (name) => {
  if (typeof cancelToken2 != typeof undefined)
    cancelToken2.cancel("Operation canceled due to new request.");

  try {
    let source = axios.CancelToken.source();
    cancelToken2 = source;

    const result = await axios({
      url: `${API_V2}/business/participants?name=${name}`,
      method: "GET",
      headers: AuthenticatedHeaders(),
      cancelToken: source.token,
    });

    return result.data;
  } catch (err) {
    return { message: err.message };
  }
};
export const deleteTeamMember = async (invitationId) => {
  try {
    const result = await fetch(`${API_V2}/invitation/${invitationId}`, {
      method: "DELETE",
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${localStorage.getItem("token")}`,
        "X-BusinessId": localStorage.getItem("businessId"),
      },
    });
    return await result.json();
  } catch (err) {
    return { message: err.message };
  }
};

export const resendInvitationMail = async (invitationId) => {
  try {
    const res = await fetch(`${API}/invitation/${invitationId}/resend-mail`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
        "X-BusinessId": localStorage.getItem("businessId"),
      },
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};
