import React, { Fragment, useState } from "react";
import {
  Menu,
  MenuItem,
  DialogTitle,
  DialogContent,
  DialogActions,
  Dialog,
  Button,
  ListItem,
  ListItemText,
  ListItemIcon,
} from "@mui/material";
import { IconButton } from "@material-ui/core";
import { MoreVert } from "@mui/icons-material";

import {
  deleteTeamMember,
  resendInvitationMail,
} from "../../../../api/TeamMembers/index";
import { useTeamMembers } from "../../../../contexts/TeamMember";
import { useToastMessage } from "../../../../contexts";
import useStyles from "../styles/muiStyles";

const TeamListItem = (props) => {
  const classes = useStyles();
  //contexts
  const { setDeleteSuccess } = useTeamMembers();
  //props
  const { title, description, invitationId, status } = props;
  // 3dot menu state
  const [anchorEl, setAnchorEl] = useState(null);

  //dialog state for delete
  const [openConfirmDialog, setOpenConfirmDialog] = useState(false);

  //function for opening confirm box for delete
  const handleClickOpen = () => {
    setOpenConfirmDialog(true);
  };
  //function for closing confirm box for delete
  const handleClickClose = () => {
    setOpenConfirmDialog(false);
  };
  //function for opening 3 dot menu
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  //function for closing 3 dot menu
  const handleClose = () => {
    setAnchorEl(null);
  };
  const { setSuccessMessage, setErrorMessage } = useToastMessage();

  //function deleteing team member
  const deleteMember = () => {
    // openLoader();
    deleteTeamMember(invitationId)
      .then((res) => {
        setDeleteSuccess(true);
        console.log(res);
        handleClose();

        if (res.error) {
          // closeLoader();
          return setErrorMessage(res.error);
        } else {
          // closeLoader();
          return setSuccessMessage("Member deleted");
        }
      })
      .catch((err) => {
        console.log(err);
        handleClose();
        setErrorMessage(err);
      });
    setDeleteSuccess(false);
  };

  const resendInvitation = async () => {
    const res = await resendInvitationMail(invitationId);
    if (res.error) {
      return setErrorMessage(res.error);
    } else {
      return setSuccessMessage(res.success);
    }
  };

  return (
    <Fragment>
      <ListItem component="div">
        <ListItemText
          primary={
            <div className="primary-block">
              <h2 className={"list-title"}>{title}</h2>
            </div>
          }
          secondary={description}
        />
        <ListItemIcon>
          <IconButton
            // disabled
            onClick={handleClick}
          >
            <MoreVert />
          </IconButton>

          {Boolean(anchorEl) && (
            <Menu
              anchorEl={anchorEl}
              open={Boolean(anchorEl)}
              onClose={handleClose}
              keepMounted
              MenuListProps={{
                "aria-labelledby": "Options",
              }}
              anchorOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              transformOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              className={classes["item-options"]}
            >
              
                <MenuItem
                  onClick={() => {
                    handleClickOpen();
                    handleClose();
                  }}
                >
                  {"Delete"}
                </MenuItem>
             
              {!status && (
                <MenuItem
                  onClick={() => {
                    resendInvitation();
                    handleClose();
                  }}
                >
                  {"Resend"}
                </MenuItem>
              )}
            </Menu>
          )}
        </ListItemIcon>
      </ListItem>
      <Dialog
        open={openConfirmDialog}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">{"Delete"}</DialogTitle>
        <DialogContent>{"Are you sure you want to delete?"}</DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              handleClickClose();
            }}
          >
            {"Cancel"}
          </Button>
          <Button
            onClick={async () => {
              await deleteMember();
              handleClickClose();
            }}
            autoFocus
          >
            {"Delete"}
          </Button>
        </DialogActions>
      </Dialog>
    </Fragment>
  );
};

export default TeamListItem;
