import React, { useEffect, useRef, useState } from "react";
import { IconButton, Button } from "@material-ui/core";
import { Search, Clear } from "@material-ui/icons";
import { motion } from "framer-motion";
import { Hidden } from "@mui/material";

import leftArrow from "../../../../assets/icons/leftArrow.svg";
import { useTeamMembers } from "../../../../contexts/TeamMember";

const TeamHeader = (props) => {
  const [isActive, setActive] = useState(false),
    { searchInvitation } = props,
    inputField = useRef(null),
    [anchorEl, setAnchorEl] = useState(null),
    open = Boolean(anchorEl),
    handleClick = (event) => {
      setAnchorEl(event.currentTarget);
    },
    handleClose = () => {
      setAnchorEl(null);
    },
    SearchVariant = {
      open: {
        opacity: 1,
      },
      closed: {
        opacity: 0,
      },
    },
    transition = { duration: 0.4, ease: [0.43, 0.13, 0.23, 0.96] };

  const { setShowInviteForm, setAcceptedFilter } = useTeamMembers();

  const variants = {
    open: { width: "100%", x: 0 },
    closed: { width: "100%", x: "80%" },
  };

  const handleSearch = (e) => {
    searchInvitation(e.target.value);
  };

  return (
    <div className="header">
      <Hidden xsDown implementation="js" only="xs">
        <div className="top">
          <Button
            variant="contained"
            type="button"
            className="btn-action"
            onClick={() => setShowInviteForm(true)}
          >
            {"Invite Team Member"}
          </Button>
        </div>
      </Hidden>
      <div className="below">
        {!isActive && (
          <div className="left">
            <Hidden smUp implementation="js">
              <a href="/business-settings">
                <img src={leftArrow} style={{ margin: "10px" }} alt="back" />
              </a>
            </Hidden>
            <h2>{`Team Members`}</h2>
          </div>
        )}
        <div className="middle">
          <motion.div
            animate={isActive ? "open" : "closed"}
            transition={transition}
            variants={SearchVariant}
            className={`search-wrapper${isActive ? " active" : ""}`}
          >
            <div className="search-block">
              <Search className="icon" />
              <input
                onChange={handleSearch}
                placeholder="Search People"
                className="search-input"
              />
              <IconButton
                className="clear"
                onClick={() => {
                  searchInvitation("");
                  setActive(!isActive);
                }}
              >
                <Clear />
              </IconButton>
            </div>
          </motion.div>
          {/* Search Icon */}
          {!isActive && (
            <IconButton
              className="search-toggle"
              onClick={() => {
                return setActive((prevState) => !prevState);
              }}
            >
              <Search />
            </IconButton>
          )}
        </div>
      </div>
    </div>
  );
};

export default TeamHeader;
