/* eslint-disable react-hooks/exhaustive-deps */
import { Fragment, useEffect, useRef, useState } from "react";
import { Divider, Button } from "@mui/material";
import { Hidden } from "@material-ui/core";
import * as actions from "../../../Store/actions/index";
import { connect } from "react-redux";

import { useToastMessage } from "../../../contexts/ToastMessageContext";

// import TeamHeader from "../../../Components/BusinessSettings/TeamMembers/TeamHead";
// import TeamListItem from "../../../Components/BusinessSettings/TeamMembers/TeamListItem";
import { useTeamMembers } from "../../../contexts/TeamMember";
import { getUserInvites, getSearchInvitation } from "../../../api/Invite/index";
import leftArrow from "../../../assets/icons/leftArrow.svg";

import TeamHeader from "./TeamHeader";
import TeamFooter from "./TeamFooter";
import TeamListItem from "./TeamListItem";
import useStyles from "./styles/muiStyles";

const TeamMembersContainer = ({ invitedUsers, getAllUserInvites }) => {
  const classes = useStyles();
  const toastMessage = useToastMessage();
  const [search, setSearch] = useState("");
  const [loading, setLoading] = useState(true);
  const {
    inviteSuccess,
    invitedMembers,
    setInvitedMembers,
    deleteSuccess,
    acceptedFilter,
    pageNo,
    setPageNo,
    currentPageRecord,
    setShowInviteForm,
    setCurrentPageRecord,
  } = useTeamMembers();
  const businessId = localStorage.getItem("businessId");
  const invitedUsersRef = useRef(() => {
    !invitedUsers && getAllUserInvites(toastMessage, businessId);
  });

  useEffect(() => {
    invitedUsersRef.current();
  }, []);

  useEffect(() => {
    getUserInvites(businessId, pageNo)
      .then((data) => {
        data.success && setLoading(false);
        console.log('data.payload', data.payload);
        setInvitedMembers(data.payload.data);
        setCurrentPageRecord(data?.payload.data.length);
      })
      .catch((err) => {
        console.log(err);
      });
  }, [businessId, pageNo, inviteSuccess, deleteSuccess]);

  return (
    <Fragment>
      {/* Mobile View */}
      <Hidden smUp implementation="js">
        <div className={classes["mobile-settings"]}>
          {invitedMembers && invitedMembers.length === 0 && (
            <div className="header">
              <a href="/business-settings">
                <img alt="back" src={leftArrow} style={{ margin: "0 10px" }} />
              </a>
              <h1 className="title">{"Team Members"}</h1>
            </div>
          )}
          <div className="content">
            <div className="members-container">
              {invitedMembers && invitedMembers.length === 0 ? (
                <div className="default-block">
                  <div className="cta">
                    <Button
                      variant="contained"
                      type="button"
                      className="btn-action"
                      onClick={() => setShowInviteForm(true)}
                    >
                      {"Invite Team Member"}
                    </Button>
                  </div>
                  <div className="info">
                    <p>
                      {
                        "You don’t have any team members yet. Invite a team member and start working in collaboration."
                      }
                    </p>
                  </div>
                </div>
              ) : (
                <div className="card-group">
                  <TeamHeader searchInvitation={(value) => setSearch(value)} />
                  <Divider className="full" />
                  <div className="card-content">
                    {invitedMembers &&
                      invitedMembers
                        .filter((element: any) =>
                          element?.name
                            ?.toLowerCase()
                            .includes(search?.toLowerCase())
                        )
                        .filter((element: any) => {
                          if (acceptedFilter === "ACCEPTED") {
                            return element.status;
                          }
                          if (acceptedFilter === "PENDING") {
                            return !element.status;
                          } else {
                            return element;
                          }
                        })
                        .map(
                          (e: { name: any; status: any; id: any }, i: any) => {
                            return (
                              <TeamListItem
                                key={i}
                                title={e.name}
                                description={e.status}
                                invitationId={e.id}
                                status={e.status}
                              />
                            );
                          }
                        )}
                  </div>
                  <TeamFooter />
                </div>
              )}
            </div>
          </div>
        </div>
      </Hidden>
      {/* Table/Desktop View */}
      <Hidden xsDown implementation="js" only="xs">
        {!loading && (
          <div className={classes["members-container"]}>
            {invitedMembers && invitedMembers.length === 0 ? (
              <div className="card-group default">
                <div className="inner-block">
                  <div className="cta">
                    <Button
                      variant="contained"
                      type="button"
                      className="btn-action"
                      onClick={() => setShowInviteForm(true)}
                    >
                      {"Invite Team Member"}
                    </Button>
                  </div>
                  <div className="info">
                    <p>
                      {
                        "You don’t have any team members yet. Invite a team member and start working in collaboration."
                      }
                    </p>
                  </div>
                </div>
              </div>
            ) : (
              <div className="card-group">
                <TeamHeader searchInvitation={(value) => setSearch(value)} />
                <Divider />
                <div className="card-content">
                  {invitedMembers &&
                    invitedMembers
                      .filter((element: any) =>
                        element?.name
                          ?.toLowerCase()
                          .includes(search?.toLowerCase())
                      )
                      .filter((element: any) => {
                        if (acceptedFilter === "ACCEPTED") {
                          return element.status;
                        }
                        if (acceptedFilter === "PENDING") {
                          return !element.status;
                        } else {
                          return element;
                        }
                      })
                      .map((e: { name: any; status: any; id: any }, i: any) => {
                        return (
                          <TeamListItem
                            key={i}
                            title={e.name}
                            description={e.status}
                            invitationId={e.id}
                            status={e.status}
                          />
                        );
                      })}
                </div>
              </div>
            )}
          </div>
        )}
      </Hidden>
    </Fragment>
  );
};

//export default TeamMembersContainer;

const mapStateToProps = (state) => {
  return {
    invitedUsers: state.invite.invitedUsers,
    selectedBusiness: state.business.selectedBusiness,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    sendUserInvite: (data, businessId, toastMessage) =>
      dispatch(actions.sendInvite(data, businessId, toastMessage)),
    getAllUserInvites: (toastMessage, businessId) =>
      dispatch(actions.getAllUserInvites(toastMessage, businessId)),
    searchInvitation: (toastMessage, businessId, searchMember) =>
      dispatch(
        actions.searchInvitation(toastMessage, businessId, searchMember)
      ),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TeamMembersContainer);
