/* eslint-disable no-unreachable */
import { Fragment, useEffect, useRef, useState } from "react";
import { IconButton, Button } from "@mui/material";
import PhoneInput from "react-phone-input-2";
import { Checkbox, FormControlLabel } from "@material-ui/core";

import { UIButton } from "../../../../Components/exports";
import { CustomInputField } from "../../../../Components/UI/CustomTextField/CustomTextField";
import leftArrow from "../../../../assets/icons/leftArrow.svg";
import Loader from "../../../../shared-components/loader";
import { sendUserInvite } from "../../../../api/Invite/index";
import { useTeamMembers } from "../../../../contexts/TeamMember";
import { useToastMessage } from "../../../../contexts";
import { useWaitingLoader } from "../../../../contexts/waitingLoaderContext";
import { useBusiness } from "../../../../contexts/BusinessProvider";

import "react-phone-input-2/lib/material.css";
import useStyles from "./styles/muiStyles";

const TeamMembersInviteForm = () => {
  const classes = useStyles();
  const { inviteSuccess, showInviteForm, setInviteSuccess, setShowInviteForm } =
    useTeamMembers();
  const [showForm, setShowForm] = useState(false);

  const { setSuccessMessage, setErrorMessage } = useToastMessage();

  const { openLoader, closeLoader } = useWaitingLoader();

  const { selectedBusiness, fetchDefaultBusiness } = useBusiness();

  const [data, setData] = useState({
    name: "",
    number: "",
    email: "",
    plan_category: ["engage"],
  });

  const { number, plan_category } = data;

  // if (selectedBusiness && selectedBusiness.ordersDetail.length === 1) {
  //   let planCate = selectedBusiness.ordersDetail[0]["category"];
  //   if (plan_category.length === 0) {
  //     console.log("plan_category", planCate);
  //     setData({ ...data, plan_category: [planCate] });
  //   }
  // }

  const inputChangedHandler = (e) => {
    let name = e.target.name;
    let value = e.target.value;
    setData({ ...data, [name]: value });
  };

  const inputChangedFlag = (event) => {
    setData({ ...data, number: event });
  };

  const submit = async (e) => {
    e.preventDefault();
    openLoader();
    const res = await sendUserInvite(data);
    if (!res.success) {
      closeLoader();
      setInviteSuccess(false);
      return setErrorMessage(
        res.messages ? res.messages.errors[0].message : res.message
      );
    } else {
      closeLoader();
      setShowInviteForm(false);
      setInviteSuccess(true);
      /*       if(invitedMembers){
      let newData = [...invitedMembers, send.invitedUser];
      console.log('newData', newData);
      setInvitedMembers(newData);
      } */
      return setSuccessMessage(res.message ?? "Invite Sent Successfully");
    }
  };
  useEffect(() => {
    setTimeout(() => setShowForm(true), 200);
  }, []);

  const HandleEngagePlatforms = (e) => {
    const { checked, value } = e.currentTarget;
    let plan_category = checked
      ? [...plan_category, value]
      : plan_category.filter((val) => val !== value);
    setData({ ...data, plan_category: plan_category });
  };

  const loadDefaultBusiness = useRef(() => {
    !selectedBusiness && fetchDefaultBusiness();
  });

  useEffect(() => {
    loadDefaultBusiness.current();
  }, []);

  return (
    <Fragment>
      <div className={classes.overlay} />
      <div className={`${classes["invite-form"]}${showForm ? " active" : ""}`}>
        {inviteSuccess && (
          <div className="invite-success">
            <div className="icon">
              <svg
                width="55"
                height="64"
                viewBox="0 0 55 64"
                fill="none"
                xmlns="//www.w3.org/2000/svg"
              >
                <path
                  d="M9.7043 54.1611C-2.37669 41.9231 -2.37669 22.0816 9.7043 9.84371C21.6174 -2.22422 40.8298 -2.39183 52.9458 9.34087L53.4534 9.84371L54.6004 11.0055L49.782 15.9999L48.579 14.7814C39.1901 5.27052 23.9676 5.27052 14.5787 14.7814C5.18974 24.2924 5.18974 39.7125 14.5787 49.2234C23.8136 58.5783 38.6926 58.7316 48.1127 49.6834L48.579 49.2234L49.8016 47.985L54.6786 52.92C54.4546 53.1469 54.0464 53.5605 53.4534 54.1611C41.3725 66.3989 21.7854 66.3989 9.7043 54.1611Z"
                  fill="#12B0EE"
                />
                <path
                  d="M46.3522 19.3796L51.3791 24.3677L27.8629 48.4288L15.5332 35.8135L20.56 30.8254L27.8614 38.2969L46.3522 19.3796Z"
                  fill="#12B0EE"
                />
              </svg>
            </div>
            <h3 className="heading">{"Invitation is Sent"}</h3>
            <div className="actions">
              <UIButton
                type="button"
                className="ms-0 invite-another"
                onClick={() => {
                  setInviteSuccess(false);
                  setShowInviteForm(true);
                }}
              >
                {"Invite another team member"}
              </UIButton>
              <Button
                variant="contained"
                className={"close-btn"}
                onClick={() => {
                  setShowForm(false);
                  setTimeout(() => setShowInviteForm(false), 200);
                }}
              >
                {"Close"}
              </Button>
            </div>
          </div>
        )}
        {!inviteSuccess && showInviteForm && (
          <Fragment>
            <div className="header">
              <IconButton
                aria-label="Back"
                className="back-btn"
                onClick={() => {
                  setShowForm(false);
                  setTimeout(() => setShowInviteForm(false), 200);
                }}
                sx={{ color: "transparent", p: 0 }}
              >
                <img
                  alt="back"
                  src={leftArrow}
                  style={{ margin: "7.5px 10px" }}
                />
              </IconButton>
              <h1 className="title">{"Invite a Team Member"}</h1>
            </div>
            <Loader label="Sending Invite" />
            <form onSubmit={(e) => submit(e)}>
              {/* <div className="col-10 col-sm-6 col-md-12">
                {selectedBusiness &&
                  selectedBusiness.ordersDetail.map((order) => {
                    return (
                      <FormControlLabel
                        className="form-label"
                        control={
                          <Checkbox
                            value={order.category}
                            defaultChecked={
                              selectedBusiness.ordersDetail.length === 1
                                ? true
                                : false
                            }
                            color="default"
                            onChange={(e) => HandleEngagePlatforms(e)}
                            disabled={
                              selectedBusiness.ordersDetail.length === 1
                                ? true
                                : false
                            }
                          />
                        }
                        label={order.category}
                      />
                    );
                  })}
              </div> */}

              <div className="field">
                <CustomInputField
                  type="text"
                  placeholder="Name"
                  name="name"
                  rows={1}
                  onChange={(e) => inputChangedHandler(e)}
                  required
                />
              </div>
              <div className="field">
                <CustomInputField
                  type="text"
                  placeholder="Email"
                  name="email"
                  rows={1}
                  onChange={(e) => inputChangedHandler(e)}
                  required
                />
              </div>
              <div className="field">
                <PhoneInput
                  country={"in"}
                  value={number}
                  onChange={(phone) => inputChangedFlag(phone)}
                  containerStyle={{ width: "100%" }}
                  inputStyle={{ borderRadius: "8px" }}
                  required
                />
              </div>
              <div className="actions">
                <Button
                  variant="contained"
                  className={"cancel-btn"}
                  onClick={() => setShowInviteForm(false)}
                >
                  {"Cancel"}
                </Button>
                <UIButton type="submit" className="ms-0">
                  {"Send Invite"}
                </UIButton>
              </div>
            </form>
          </Fragment>
        )}
      </div>
    </Fragment>
  );
};

export default TeamMembersInviteForm;
